"use client";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Label } from "@/components/ui/label";
import React, { useState } from "react";
import Navbar from "../../../../components/Navbar";
import { useRouter } from "next/navigation";

// Types
interface PersonalDetailsData {
  fullName: string;
  dateOfBirth: string;
  gender: string;
  email: string;
  address: string;
}

interface AcademicDetailsData {
  tenth: {
    schoolName: string;
    board: string;
    yearOfPassing: string;
    percentage: string;
  };
  twelfth: {
    schoolName: string;
    board: string;
    yearOfPassing: string;
    percentage: string;
  };
  graduation: {
    collegeName: string;
    university: string;
    degree: string;
    percentage: string;
  };
}

interface DocumentsData {
  photo: File | null;
  tenthMarksheet: File | null;
  twelfthMarksheet: File | null;
  graduationMarksheet: File | null;
}

const steps: string[] = [
  "Personal Details",
  "Academic Details",
  "Documents Upload",
];

const AdmissionForm: React.FC = () => {
  const router = useRouter();
  const [activeStep, setActiveStep] = useState<number>(0);
  const [personalDetails, setPersonalDetails] = useState<PersonalDetailsData>({
    fullName: "",
    dateOfBirth: "",
    gender: "",
    email: "",
    address: "",
  });

  const [academicDetails, setAcademicDetails] = useState<AcademicDetailsData>({
    tenth: {
      schoolName: "",
      board: "",
      yearOfPassing: "",
      percentage: "",
    },
    twelfth: {
      schoolName: "",
      board: "",
      yearOfPassing: "",
      percentage: "",
    },
    graduation: {
      collegeName: "",
      university: "",
      degree: "",
      percentage: "",
    },
  });

  const [documents, setDocuments] = useState<DocumentsData>({
    photo: null,
    tenthMarksheet: null,
    twelfthMarksheet: null,
    graduationMarksheet: null,
  });

  const [error, setError] = useState<string>("");

  const handleNext = (): void => {
    setActiveStep((prevStep) => prevStep + 1);
  };

  const handleBack = (): void => {
    setActiveStep((prevStep) => prevStep - 1);
  };

  const handleCancel = (): void => {
    router.back();
  };

  const handlePersonalDetailsChange = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>,
  ): void => {
    const { name, value } = event.target;
    setPersonalDetails((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleGenderChange = (value: string): void => {
    setPersonalDetails((prev) => ({
      ...prev,
      gender: value,
    }));
  };

  const handleFileChange = (
    event: React.ChangeEvent<HTMLInputElement>,
    field: keyof DocumentsData,
  ): void => {
    const file = event.target.files?.[0] || null;
    setDocuments((prev) => ({
      ...prev,
      [field]: file,
    }));
  };

  const PersonalDetails: React.FC = () => (
    <div className={`${activeStep === 0 ? "block" : "hidden"} space-y-6`}>
      <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="fullName">Full Name</Label>
          <Input
            id="fullName"
            name="fullName"
            value={personalDetails.fullName}
            onChange={handlePersonalDetailsChange}
            readOnly={false}
            required
          />
        </div>
        <div className="space-y-2">
          <Label htmlFor="dateOfBirth">Date of Birth</Label>
          <Input
            id="dateOfBirth"
            name="dateOfBirth"
            type="date"
            value={personalDetails.dateOfBirth}
            onChange={handlePersonalDetailsChange}
            readOnly={false}
            required
          />
        </div>
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label>Gender</Label>
          <Select
            onValueChange={handleGenderChange}
            value={personalDetails.gender}
          >
            <SelectTrigger>
              <SelectValue placeholder="Select gender" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="male">Male</SelectItem>
              <SelectItem value="female">Female</SelectItem>
              <SelectItem value="other">Other</SelectItem>
            </SelectContent>
          </Select>
        </div>
        <div className="space-y-2">
          <Label htmlFor="email">Email</Label>
          <Input
            id="email"
            name="email"
            type="email"
            value={personalDetails.email}
            onChange={handlePersonalDetailsChange}
            readOnly={false}
            required
          />
        </div>
      </div>

      <div className="space-y-2">
        <Label htmlFor="address">Address</Label>
        <Textarea
          id="address"
          name="address"
          value={personalDetails.address}
          onChange={handlePersonalDetailsChange}
          readOnly={false}
          required
        />
      </div>
    </div>
  );

  const AcademicDetails: React.FC = () => (
    <div className={`${activeStep === 1 ? "block" : "hidden"} space-y-6`}>
      <div className="space-y-4">
        <h3 className="text-xl font-semibold">10th Standard Details</h3>
        <div className="space-y-2">
          <Label htmlFor="tenthSchool">School Name</Label>
          <Input
            id="tenthSchool"
            value={academicDetails.tenth.schoolName}
            onChange={(e) =>
              setAcademicDetails((prev) => ({
                ...prev,
                tenth: { ...prev.tenth, schoolName: e.target.value },
              }))
            }
            readOnly={false}
            required
          />
        </div>
      </div>

      <div className="space-y-4">
        <h3 className="text-xl font-semibold">12th Standard Details</h3>
        <div className="space-y-2">
          <Label htmlFor="twelfthSchool">School Name</Label>
          <Input
            id="twelfthSchool"
            value={academicDetails.twelfth.schoolName}
            onChange={(e) =>
              setAcademicDetails((prev) => ({
                ...prev,
                twelfth: { ...prev.twelfth, schoolName: e.target.value },
              }))
            }
            readOnly={false}
            required
          />
        </div>
      </div>

      <div className="space-y-4">
        <h3 className="text-xl font-semibold">Graduation Details</h3>
        <div className="space-y-2">
          <Label htmlFor="collegeName">College Name</Label>
          <Input
            id="collegeName"
            value={academicDetails.graduation.collegeName}
            onChange={(e) =>
              setAcademicDetails((prev) => ({
                ...prev,
                graduation: { ...prev.graduation, collegeName: e.target.value },
              }))
            }
            readOnly={false}
            required
          />
        </div>
      </div>
    </div>
  );

  const DocumentUpload: React.FC = () => (
    <div className={`${activeStep === 2 ? "block" : "hidden"} space-y-6`}>
      <div className="space-y-4">
        <h3 className="text-xl font-semibold">Upload your Photograph</h3>
        <Input
          type="file"
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            handleFileChange(e, "photo")
          }
          required
        />
      </div>

      <div className="space-y-4">
        <h3 className="text-xl font-semibold">Upload your 10th Marksheet</h3>
        <Input
          type="file"
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            handleFileChange(e, "tenthMarksheet")
          }
          required
        />
      </div>

      <div className="space-y-4">
        <h3 className="text-xl font-semibold">Upload your 12th Marksheet</h3>
        <Input
          type="file"
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            handleFileChange(e, "twelfthMarksheet")
          }
          required
        />
      </div>

      <div className="space-y-4">
        <h3 className="text-xl font-semibold">
          Upload your Graduation Marksheet
        </h3>
        <Input
          type="file"
          accept="application/pdf"
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            const file = e.target.files?.[0];
            if (file && file.type !== "application/pdf") {
              setError("Please upload only PDF files");
              e.target.value = "";
              return;
            }
            handleFileChange(e, "graduationMarksheet");
          }}
          required
        />
      </div>

      {error && (
        <div className="bg-destructive text-destructive-foreground p-4 rounded">
          {error}
        </div>
      )}
    </div>
  );

  const getStepContent = (step: number): React.ReactNode => {
    switch (step) {
      case 0:
        return <PersonalDetails />;
      case 1:
        return <AcademicDetails />;
      case 2:
        return <DocumentUpload />;
      default:
        return "Unknown step";
    }
  };

  const handleSubmit = (): void => {
    // Handle form submission
    console.log({
      personalDetails,
      academicDetails,
      documents,
    });
  };

  return (
    <>
      <Navbar />
      <div className="container mx-auto my-8 max-w-3xl">
        <Card>
          <CardHeader>
            <CardTitle className="text-center">
              MCA Admission Application
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="mb-8">
              <div className="flex justify-between">
                {steps.map((label, index) => (
                  <div key={label} className="flex items-center">
                    <div
                      className={`w-8 h-8 rounded-full flex items-center justify-center ${
                        index <= activeStep
                          ? "bg-primary text-primary-foreground"
                          : "bg-muted"
                      }`}
                    >
                      {index + 1}
                    </div>
                    <span className="ml-2">{label}</span>
                  </div>
                ))}
              </div>
            </div>

            {activeStep === steps.length ? (
              <div className="text-center">
                <p>Application submitted successfully!</p>
              </div>
            ) : (
              <>
                {getStepContent(activeStep)}
                <div className="flex justify-between mt-6">
                  <Button variant="destructive" onClick={handleCancel}>
                    Cancel
                  </Button>
                  <div className="space-x-2">
                    <Button
                      variant="outline"
                      disabled={activeStep === 0}
                      onClick={handleBack}
                    >
                      Back
                    </Button>
                    <Button
                      onClick={
                        activeStep === steps.length - 1
                          ? handleSubmit
                          : handleNext
                      }
                    >
                      {activeStep === steps.length - 1 ? "Submit" : "Next"}
                    </Button>
                  </div>
                </div>
              </>
            )}
          </CardContent>
        </Card>
      </div>
    </>
  );
};

export default AdmissionForm;
